import React from 'react';
import { MapContainer, TileLayer, Marker, Popup } from 'react-leaflet';
import postesData from '../data/postes-occ-geomatique-2.json';

const Carte2 = () => {
    console.log(postesData.features);
    return (
        <MapContainer center={[30, 70]} zoom={1.4} scrollWheelZoom={false} >
            <TileLayer
                attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
                url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
            />

            {
                postesData.features.map(poste => (
                    <Marker
                        key={poste.properties.fid}
                        position={poste.geometry.coordinates}>
                        <Popup position={poste.geometry.coordinates}>
                            <div>{"Poste n" + poste.properties.fid + " : " + poste.properties.poste}
                            </div>
                            <div>{poste.properties.dates}
                            </div>
                            <div>
                                {"(" + poste.properties.duree + ")"}
                            </div>
                            <div>{poste.properties.entreprise}
                            </div>
                            <div>{poste.properties.ville}
                            </div>

                        </Popup>

                    </Marker>

                ))
            }
        </MapContainer >
    );
};

export default Carte2;
