import React from 'react';
import { BrowserRouter, Routes, Route } from "react-router-dom";
import Header from './components/Header';
import Footer from './components/Footer';
import Competences from './pages/Competences';
import Devweb from './pages/Devweb';
import Environnement from './pages/Environnement';
import Formation from './pages/Formation';
import Geomcarto from './pages/Geomcarto';
import Home from './pages/Home';
import Page404 from './pages/Page404';

const App = () => {
    return (
        <BrowserRouter>
            <Header />
            <Routes>
                <Route path="/" element={<Home />} />
                <Route path="/competences" element={<Competences />} />
                <Route path="/competence-dev-web" element={<Devweb />} />
                <Route path="/competence-geomatique-cartographie" element={<Geomcarto />} />
                <Route path="/competence-geographie-environnement" element={<Environnement />} />
                <Route path="/formation" element={<Formation />} />
                {/*path="*" fonctionne si jamais l'url ne correspond  rien de dclarer au dessus*/}
                <Route path="*" element={<Page404 />} />
            </Routes>
            <Footer />
        </BrowserRouter>
    );
};

export default App;