import React from 'react';
import { Link } from 'react-router-dom';
import BtnTelechargerCv from './BtnTelechargerCv';

const Footer = () => {
    return (
        <footer className="footer">
            <div className="footer__main-part">
                <div className="footer__main-part__plan">
                    <h4>PLAN DU SITE</h4>
                    <ul className="footer__main-part__plan__ul">
                        <li className="footer__main-part__plan__ul__li">
                            <Link
                                to='/'>
                                ACCUEIL
                            </Link>
                        </li>
                        <li className="footer__main-part__plan__ul__p-w">-</li>
                        <li className="footer__main-part__plan__ul__li">
                            <Link
                                to='/competences'>
                                COMPETENCES
                            </Link>
                        </li>
                        <li className="footer__main-part__plan__ul__p-w">-</li>
                        <li className="footer__main-part__plan__ul__li">
                            <Link
                                to='/formation'>
                                FORMATION
                            </Link>
                        </li>
                    </ul>
                </div>
                <div className="footer__main-part__icons">
                    <BtnTelechargerCv />
                    <div className="footer__main-part__icons__pages">
                        <p className="p-lora-w">| Mes pages</p>
                        <a href="https://www.linkedin.com/in/marianne-bricquir/" target="_blank"><i className="fa-brands fa-linkedin"></i></a>
                        <a href="https://github.com/MarianneBricquir?tab=repositories" target="_blank"> <i className="fa-brands fa-github"></i ></a>
                    </div>
                </div>
            </div>
            <p className="footer__p-w-s">Site web cr par Marianne Bricquir, 2022</p>
        </footer >
    );
};

export default Footer;