import React from 'react';

const BtnTelechargerCv = () => {
    const onButtonClick = () => {
        // using Java Script method to get PDF file
        fetch('2022_cv_marianneBricquir.pdf').then(response => {
            response.blob().then(blob => {
                // Creating new object of PDF file
                const fileURL = window.URL.createObjectURL(blob);
                // Setting various property values
                let alink = document.createElement('a');
                alink.href = fileURL;
                alink.download = '2022_cv_marianneBricquir.pdf';
                alink.click();
            })
        })
    }
    return (
        <button className="btn-cv" onClick={onButtonClick}>Tlcharger mon CV</button>
    );
};

export default BtnTelechargerCv;